s la aproximación considerada más válida cuando la oferta se dirige a segmentos especialmente sensibles al precio.")])]),_c('tr',{attrs:{"data-aos":"fade-right"}},[_c('th',{staticStyle:{"background-color":"#FFF4E2"}},[_vm._v("Estrategia de personalización de precios")]),_c('td',{staticStyle:{"background-color":"#FFF4E2"}},[_vm._v("Internet permite que se observen los comportamientos de compra y demanda dentro del "),_c('em',[_vm._v("website")]),_vm._v(". Con una plataforma adecuada de "),_c('em',[_vm._v("marketing one-to-one")]),_vm._v(", se pueden diseñar planes específicos por grupos de clientes, al igual que promociones especiales, promociones cruzadas, o descuentos, además de ofrecer valores muy apreciados por los consumidores (información personalizada, alertas de posibles ofertas o nuevos productos, mejor estructuración de la oferta de acuerdo con preferencias). La personalización siempre es recomendable, en mayor o en menor medida, aunque no es la única solución a la hora de diseñar una estrategia de precios imbatibles; el consumidor puede acceder a servicios específicos que “buscan y comparan” productos, ofertas y establecimientos de forma automática. Cuando no hay control absoluto en la personalización, la publicidad programática aprovecha la estrategia. La tecnología detecta el público que navega en la red con inquietudes concretas e impacta con mensajes de oportunidad única. Basándose en el "),_c('em',[_vm._v("big data")]),_vm._v(", el anunciante compra audiencias y no espacios, como en la publicidad tradicional, segmentando, ofreciendo y llegando a las personas indicadas.")])]),_c('tr',{attrs:{"data-aos":"fade-right"}},[_c('th',{staticStyle:{"background-color":"#FFF"}},[_vm._v("Estrategia de precios descremados")]),_c('td',{staticStyle:{"background-color":"#FFF"}},[_vm._v("Cuando se ofrece un producto o servicio innovador o exclusivo, se tiende a elegir la alternativa estratégica de “precios más altos”, pretendiendo captar a los primeros interesados, segmento dispuesto a probar el producto, que no son especialmente sensibles al precio. Posteriormente, se van reduciendo paulatinamente los precios intentando captar a más segmentos, aunque usualmente estas reducciones suelen estar motivadas por las estrategias de la competencia.")])]),_c('tr',{attrs:{"data-aos":"fade-right"}},[_c('th',{staticStyle:{"background-color":"#FFF4E2"}},[_vm._v("Estrategia de precios dinámicos")]),_c('td',{staticStyle:{"background-color":"#FFF4E2"}},[_vm._v("Es una estrategia de personalización completa de los precios, ya que estos varían para cada producto, cada consumidor y cada transacción. Es, además, el medio más adecuado para mejorar la experiencia de compra del usuario (personalización e interactividad, y factores psicológicos como el sentimiento de pertenencia a una comunidad, la participación, el entusiasmo por la puja y la determinación de condiciones). Los precios dinámicos atraen a los consumidores, fomentan la repetición de compra, e incrementan el tiempo pasado en el sitio, lo cual repercute en las métricas, y en la explotación de la rentabilidad de estas. Junto a estos factores, los precios dinámicos también ayudan a reducir costes e incrementar beneficios a través de la creación de mercados más eficientes. Se emplean especialmente para productos perecederos y estandarizados, en inventarios de difícil salida a los que hay que diseñar estrategias ventajosas de comercialización. Sus aplicaciones principales son las subastas y los modelos basados en la agregación de demanda (precios escalonados).")])]),_c('tr',{attrs:{"data-aos":"fade-right"}},[_c('th',{staticStyle:{"background-color":"#FFF"}},[_vm._v("Estrategia de venta de soluciones")]),_c('td',{staticStyle:{"background-color":"#FFF"}},[_vm._v("Una de las características fundamentales de un entorno como Internet es la posibilidad de ofrecer soluciones específicas para un determinado mercado objetivo. Si se define una propuesta de valor desde el punto de vista de la solución que